<template>
    <div>
        <el-form :inline=true :model="form" ref="form">
            <el-row :gutter="5">
                <el-col :span="24">
                    <el-form-item label="学号" label-width="68px">
                        <el-input v-model="form.studentid"></el-input>
                    </el-form-item>
                    <el-form-item label="姓名" label-width="68px">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="宿舍" label-width="68px">
                        <el-input v-model="form.dorm"></el-input>
                    </el-form-item>
                    <el-form-item label="班级" label-width="68px">
                        <el-select v-model="form.classname" filterable>
                            <el-option v-for="item in classList" :key="item.value" :label="item.label"
                                :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" size="mini" @click="searchStudent">搜索</el-button>
                        <el-button size="mini" @click="resetFields('form')">重置</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <el-table v-loading="tableDataLoading" :data="tableData" border>
            <el-table-column prop="studentid" label="学号" width="180">
            </el-table-column>
            <el-table-column prop="name" label="姓名" width="180">
            </el-table-column>
            <el-table-column prop="phone" label="手机号">
            </el-table-column>
            <el-table-column prop="dorm" label="宿舍">
            </el-table-column>
            <el-table-column prop="address" label="家庭住址">
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="small" type="primary" @click="showStudent(scope)" icon="el-icon-view" circle>
                    </el-button>
                    <el-button size="small" type="danger" @click="deleteStudent(scope)" icon="el-icon-delete" circle>
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <div align="right">
            <el-pagination
            @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="10" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import axios from "axios"

    export default {
        name: "studentList",
        data() {
            return {
                form: {
                    studentid: "",
                    name: "",
                    classname: "",
                    dorm: ""
                },
                classList: [],
                tableDataLoading: false,
                tableData: [],
                currentPage: 1,
                total: 0
            }
        },
        mounted() {
            // 挂载
            this.getClassNameList();
            this.getStudentList(1);
        },
        methods: {
            searchStudent() {
                // 查询学生
                this.tableDataLoading = true;
                var formData = new FormData();
                formData.append("studentid", this.form.studentid);
                formData.append("name", this.form.name);
                formData.append("classname", this.form.classname);
                formData.append("dorm", this.form.dorm);
                axios
                    .post("/user/searchStudent", formData)
                    .then(res => {
                        this.tableDataLoading = false;
                        var result = res.data;
                        if (result.code == 200) {
                            this.tableData = result.list;
                            return;
                        }
                        this.$message({
                            showClose: true,
                            message: '获取学生信息错误',
                            type: 'error'
                        });
                    })
                    .catch(e => {
                        this.tableDataLoading = false;
                        this.$message({
                            showClose: true,
                            message: '获取学生信息错误',
                            type: 'error'
                        });
                    })
            },
            getStudentList(page) {
                // 获取学生信息
                this.tableDataLoading = true;
                axios
                    .get("/user/StudentList?page=" + page)
                    .then(res => {
                        this.tableDataLoading = false;
                        var result = res.data;
                        if (result.code == 200) {
                            this.tableData = result.list;
                            this.total = result.total;
                            return;
                        }
                        this.$message({
                            showClose: true,
                            message: '获取学生信息错误',
                            type: 'error'
                        });
                    })
                    .catch(e => {
                        this.tableDataLoading = false;
                        this.$message({
                            showClose: true,
                            message: '获取学生信息错误',
                            type: 'error'
                        });
                    })
            },
            getClassNameList() {
                // 获取班级列表
                axios
                    .get("/user/ClassNameList")
                    .then(res => {
                        var result = res.data;
                        if (result.code == 200) {
                            this.classList = result.list;
                            return;
                        }
                        this.$message({
                            showClose: true,
                            message: '获取班级信息失败',
                            type: 'error'
                        });
                    })
                    .catch(e => {
                        this.$message({
                            showClose: true,
                            message: '获取班级信息失败',
                            type: 'error'
                        });
                    })
            },
            resetFields(form) {
                this.form.studentid = ""
                this.form.name = ""
                this.form.classname = ""
                this.form.classList = []
            },
            showStudent(scope) {
                console.log(scope.row)
            },
            deleteStudent(scope) {
                var studentid = scope.row.studentid;
                axios
                    .delete("/user/deleteStudent?studentid=" + studentid)
                    .then(res => {
                        var result = res.data;
                        if (result.code == 200) {
                            this.getStudentList(this.currentPage);
                            this.$message({
                                message: '删除学生成功！',
                                type: 'success'
                            });
                            return;
                        }
                        this.$message.error("删除学生失败！");
                    })
                    .catch(e => {
                        this.$message.error("请联系管理员！");
                    })
            },
            handleCurrentChange(val) {
                // 当页数变换的时候
                this.getStudentList(val);
            }
        }
    }
</script>

<style scoped>
    .el-input {
        width: 12rem;
    }
</style>